import {store, tileNames,getmapTile} from './stores'

export const actionsCode={
    CHANGE_SELECTED_BUTTON: "CHANGE_SELECTED_BUTTON",
    ADD_PICTURE_IN_TD: "ADD_PICTURE_IN_TD",
    REMOVE_PICTURE_IN_TD: "REMOVE_PICTURE_IN_TD"
};

export const actionsMap={
    REDUCE_HP: "REDUCE_HP",
    LOAD_MAP: "LOAD_MAP",
    ZOOM: "ZOOM"
};

export const actionsTypes ={
    MAP: actionsMap,
    CODE: actionsCode
};


//----------------------------------------------

export const changeSelectedButton = (index, className) =>{
    return{
        type: actionsCode.CHANGE_SELECTED_BUTTON,
        payload: {
            index: index,
            className: className
        }
    }
};

export const addPictureInTD = (className , indexRow) => {
    return{
        type: actionsCode.ADD_PICTURE_IN_TD,
        payload: {
            className: className,
            indexRow: indexRow
        }
    };
};

export const removePictureInTD = (className , indexRow) => {
    return{
        type: actionsCode.REMOVE_PICTURE_IN_TD,
        payload: {
            className: className,
            indexRow: indexRow
        }
    };
};

//-------------------------------------------------

export const reduceHP = (i, j) => {
    return {
        type: actionsMap.REDUCE_HP,
        payload: {
            i:i,
            j:j
        }
    }
};

function getDirection(direction){
    switch (direction) {
        case "North":
            return 0;
        case "East":
            return 900;
        case "South":
            return 180;
        case "West":
            return -90;
        case "NorthWest":
            return -45;
        case "NorthEast":
            return 45;
        case "SouthWest":
            return -135;
        case "SouthEast":
            return 135;
    }
    return 0;
}

export const loadMap = (text) => {
    let xmlDoc = null;

    try {

        if (window.DOMParser) {
            // code for modern browsers
            const parser = new DOMParser();
            xmlDoc = parser.parseFromString(text,"text/xml");
        }
        else {
            // code for old IE browsers
            // eslint-disable-next-line
            xmlDoc = new ActiveXObject("Microsoft.XMLDOM");
            xmlDoc.async = false;
            xmlDoc.loadXML(text);
        }

        const world = xmlDoc.getElementsByTagName('World')[0];
        const width = Number(world.getAttribute('Width'));
        const height = Number(world.getAttribute('Height'));
        const shellDistance = Number(world.getAttribute('ShellDistance'));

        const tankEl = world.getElementsByTagName('Tank')[0];
        const tank = {
            X: Number(tankEl.getAttribute('X')),
            Y: Number(tankEl.getAttribute('Y')),
            TankDirection: getDirection(tankEl.getElementsByTagName('TankDirection')[0].innerHTML),
            TurretDirection: getDirection(tankEl.getElementsByTagName('TurretDirection')[0].innerHTML),
            TurretStatus: getDirection(tankEl.getElementsByTagName('TurretStatus')[0].innerHTML),
        };
        const units = world.getElementsByTagName('Units')[0].children;

        let map = [];
        for (let i = 0 ; i < height; i++){
            let row = [];
            for (let j = 0 ; j < width; j++) {
                row.push([]);
            }
            map.push(row)
        }


        for(let c = 0 ; c < units.length; c++){
            const X = Number(units[c].getAttribute('X'));
            const Y = Number(units[c].getAttribute('Y'));
            let XShift = units[c].getAttribute('XShift');
            if (XShift)
                XShift = Number(units[c].getAttribute('XShift'));
            let YShift = units[c].getAttribute('YShift');
            if (YShift)
                YShift = Number(units[c].getAttribute('YShift'));

            let tile = tileNames.empty;
            switch (units[c].tagName) {
                case "Swamp":{
                    tile = (tileNames.swamp);
                    break;
                }
                case "Rock":{
                    tile = (tileNames.mountains);
                    break;
                }
                case "LittleRock":{
                    tile = (tileNames.littleMountains);
                    break;
                }
                case "LittleSwamp":{
                    tile = (tileNames.littleSwamp);
                    break;
                }
                case "LittlePillbox":{
                    tile = (tileNames._1hp);
                    break;
                }
                case "MediumPillbox":{
                    tile = (tileNames._2hp);
                    break;
                }
                case "BigPillbox":{
                    tile = (tileNames._3hp);
                    break;
                }
            }
            map[Y][X].push({ XShift: XShift, YShift: YShift, tile: tile});
        }
        return {
            type: actionsMap.LOAD_MAP,
            payload: {
                shellDistance: shellDistance,
                tank: tank,
                mapWidth: width,
                mapHeight: height,
                map: map
            }
        }
    }
    catch (e) {
        console.log('Ошибка файл карты неправильно сформирован');
    }

    return {
        type: ""
    }

};

export const zoom = (value) =>{
    return {
        type: actionsMap.ZOOM,
        payload: value
    }
};

