import React, {Component} from "react";
import ReactModal from "react-modal";
import {connect, Provider} from "react-redux";
import {store} from '../store/stores.js'
import {putActionsToProps_Code, putStateToProps_Code} from "../store/reducers.js";

class Menu extends Component {
    render() {
        return (
            <div>
                <MenuButtons />
                <MenuGame />
            </div>
        )
    }
}

class MenuButtons extends Component {
    render() {
        return (
            <div className="flex-row margin-5">
                <div className="flex-column flex-justify-start widthAll">
                    <div className="flex-column">
                        <span>Скорость воспроизведения</span>
                        <div>
                            {/*<input type="range" id="speedInput"*/}
                                   {/*onInput="document.getElementById('speedVal').innerText = document.getElementById('speedInput').value" />*/}
                            {/*<span id="speedVal">50</span>*/}
                        </div>
                        <SpeedRange />
                    </div>
                    <div id="moveBtnDiv" className="flex-row">
                        <div className="flex-row">
                            <MenuButton image="images/menu/startbtn.svg" OnClick={() => alert('1')} />
                            <MenuButton image="images/menu/pausebtn.svg" OnClick={() =>alert('2')} />
                            <MenuButton image="images/menu/stopbtn.svg" OnClick={() =>alert('3')} />
                            <MenuButton image="images/menu/skipbtn.svg" OnClick={() =>alert('4')} />
                            <MenuButton image="images/menu/nextstepbtn.svg" OnClick={() =>alert('5')} />
                        </div>
                        <div style={{width: 50}}> </div>
                        <div className="flex-row">
                            <MenuButton image="images/menu/savebtn.svg" OnClick={() => alert('6')} />
                            <MenuButton image="images/menu/loadbtn.svg" OnClick={() => alert('7')} />
                        </div>
                        <MenuButton image="" OnClick={() => alert('8')} />
                    </div>
                    <div style={{textAlign: 'right'}} >
                        <span>Дальность стрельбы: </span>
                        <span>10</span>
                    </div>
                    <div id="saveBtnDiv">

                    </div>
                </div>
                <div className="flex-column flex-justify-evenly">
                    <InfoButton />
                    <TankInfo />
                </div>
            </div>
        )
    }
}

function MenuButton(props) {
    return (
        <button
            onClick={props.OnClick}
        >
            <img src={props.image} alt="" />
        </button>
    )
}

class InfoButton extends Component {
    constructor(props){
        super(props);
        this.state={
            isOpen: false
        }
    }
    render() {
        return (
            <div>
                <button id="infoButton" className="flex-row" onClick={() => this.setState({isOpen: true})}>
                    <span>INFO</span>
                    <img src="../images/menu/infobtn.svg" alt=""/>
                </button>
                <ReactModal isOpen={this.state.isOpen} onRequestClose={() => this.setState({isOpen: false})} >
                    <Info onClick={() => this.setState({isOpen: false})} />
                </ReactModal>
            </div>
        );
    }
}
function Info(props){
    return(
        <div>
            <button onClick={() => props.OnClick}>Закрыть</button>
            /*TODO*/
        </div>
    )
}

class TankInfo extends Component {
    render()
    {
        return (
            <div id="tankDiv">
                <img src="../images/game/tank.png" alt="" />
            </div>
        )
    }
}

class SpeedRange extends Component{
    render() {
        return (
            <div>
                /*TODO*/
            </div>
        )
    }
}

// ========================================

class MenuGame extends Component {
    render() {
        return (
            <div  className="flex-row flex-align-start ">
                <div id="commandsDiv" className="overflow overflow-y-visible margin-10">
                    <Provider store={store}>
                        <WrappedGameTable />
                    </Provider>
                </div>
                <div >
                    <Provider store={store}>
                        <WrappedGameButtonsTable />
                    </Provider>
                </div>
            </div>
        )
    }
}

class GameTable extends Component{
    renderRows(){
        let arr=[];
        const {rows} = this.props;
        for(let i = 0; i < rows.length; i++){
            arr.push(
                <GameTableRow
                    index={i}
                    value={rows[i]}
                    onClickTD_LMB={(className) => this.props.addPictureInTD(className, i)}
                    onClickTD_RMB={(className) => this.props.removePictureInTD(className, i)}
                />
            )
        }

        return arr;
    }

    render() {
        console.log('11');
        return (
            <table id="commands">
                <tbody>
                <tr>
                    <th>#</th>
                    <th>Наводчик</th>
                    <th>Водитель</th>
                    <th>Заряжающий</th>
                </tr>
                {this.renderRows()}
                </tbody>
            </table>
        )
    }
}

const WrappedGameTable = connect(putStateToProps_Code, putActionsToProps_Code)(GameTable);

class GameTableRow extends Component{
    render() {
        return (
            <tr>
                <td style={{width: 20}} >{this.props.index+1}</td>
                <GameTableCell
                    value={this.props.value.gunner}
                    onClickTD_LMB={() => this.props.onClickTD_LMB("gunner")}
                    onClickTD_RMB={() => this.props.onClickTD_RMB("gunner")}
                />
                <GameTableCell
                    value={this.props.value.driver}
                    onClickTD_LMB={() => this.props.onClickTD_LMB("driver")}
                    onClickTD_RMB={() => this.props.onClickTD_RMB("driver")}
                />
                <GameTableCell
                    value={this.props.value.charger}
                    onClickTD_LMB={() => this.props.onClickTD_LMB("charger")}
                    onClickTD_RMB={() => this.props.onClickTD_RMB("charger")}
                />
            </tr>
        );
    }
}

function GameTableCell(props) {
    return (
        <td className="gameTD" onClick={() => props.onClickTD_LMB()} onContextMenu={(e) => {e.preventDefault(); props.onClickTD_RMB()} }>
            <div style={props.value} />
        </td>
    );
}

// ========================================

const gunnerBtnsCount =8;
const driverBtnsCount =11;
const chargerBtnsCount =2;

class GameButtonsTable extends Component{
    renderButtons(className) {
        let arr = [];
        const {gunnerSelectedIndex,driverSelectedIndex,chargerSelectedIndex,
                changeSelectedButton} = this.props;
        switch (className) {
            case "gunner": {
                for (let i = 0; i < gunnerBtnsCount; i++) {
                    arr.push(
                        <GameButton key={`${className} - ${i}`}
                                    index={i} class={className} selected={gunnerSelectedIndex === i}
                                    onClick={() => changeSelectedButton(i, className)}/>
                    );
                }
                break;
            }
            case "driver":{
                for (let i = 0; i < driverBtnsCount; i++) {
                    arr.push(
                        <GameButton key={`${className} - ${i}`}
                                    index={i} class={className} selected={driverSelectedIndex === i}
                                    onClick={() => changeSelectedButton(i, className)}/>
                    );
                }
                break;
            }
            case "charger":{
                for (let i = 0; i < chargerBtnsCount; i++) {
                    arr.push(
                        <GameButton key={`${className} - ${i}`}
                                    index={i} class={className} selected={chargerSelectedIndex === i}
                                    onClick={() => changeSelectedButton(i, className)}/>
                    );
                }
                break;
            }
        }
        return arr;
    }

    render() {
        return (
            <div className="flex-row flex-align-start overflow">
                <div className="flex-column">
                    <div style={{height: 20}} className="padding-5">Наводчик</div>
                    <div className="gameButtonsColumn flex-row flex-wrap">
                        {this.renderButtons("gunner")}
                    </div>
                </div>
                <div className="flex-column">
                    <div style={{height: 20}} className="padding-5">Водитель</div>
                    <div className="gameButtonsColumn flex-row flex-wrap">
                        {this.renderButtons("driver")}
                    </div>
                </div>
                <div className="flex-column">
                    <div style={{height: 20, }} className="padding-5">Заряжающий</div>
                    <div className="gameButtonsColumn flex-row flex-wrap">
                        {this.renderButtons("charger")}
                    </div>
                </div>
            </div>
        )
    }
}

const WrappedGameButtonsTable = connect(putStateToProps_Code, putActionsToProps_Code)(GameButtonsTable);

function GameButton(props) {
    return (
        <button className={props.class + (props.selected? " selected":"") + " margin-1"}
                style={getGameButtonImage(props.class, props.index)}
                onClick={props.onClick}/>
    )
}

